.emailCard {


 
    cursor: pointer;
    text-align: left;
    font-size: x-small;
    border: 0.2px solid rgb(217, 217, 217);


    width: 300px;

    transition: transform 0.25s ease-out;
    height: 120px;

    
    
}
.emailCard:hover {
    transform: scale(1.02);
}

.read {
    color: rgb(79, 79, 79);
    background-color: rgb(218, 218, 218);
    padding-left: 40px;
    padding-top: 3px;
    padding-bottom: 5px;



}
.unread {
    color: rgb(0, 0, 0);
    background-color: rgb(244, 244, 244);
    padding-left: 40px;
    padding-top: 3px;
    padding-bottom: 5px;

}
 
.selected {
    /* Add your styles for the selected email card, for example, darker background */
    background-color: rgb(123, 123, 123);
    color: rgb(0, 0, 0);

}
